class CommunicationService {
  constructor() {
    this.listeners = new Set();
    this.isConnected = false;
    this.debugInfo = '...';
    this.channel = null;
    
    this.init();
  }

  //  BroadcastChannel
  init() {
    if ('BroadcastChannel' in window) {
      try {
        this.channel = new BroadcastChannel('pos_channel');
        
        this.channel.onmessage = (event) => {
          this.handleMessage(event.data);
        };

        this.isConnected = true;
        this.debugInfo = 'BroadcastChannel ';
        console.log('BroadcastChannel ');
        
      } catch (error) {
        console.error('BroadcastChannel :', error);
        this.debugInfo = 'BroadcastChannel ';
      }
    } else {
      console.warn('BroadcastChannel ');
      this.debugInfo = 'BroadcastChannel ';
    }
  }

  // 
  sendMessage(type, data) {
    if (!this.isConnected || !this.channel) {
      console.error('BroadcastChannel ');
      this.debugInfo = 'BroadcastChannel ';
      return;
    }

    const message = { type, data, timestamp: Date.now() };
    
    try {
      this.channel.postMessage(message);
      this.debugInfo = ` ${type}`;
      console.log(':', message);
    } catch (error) {
      console.error(':', error);
      this.debugInfo = '';
    }
  }

  // 
  handleMessage(message) {
    console.log(':', message);
    this.debugInfo = ` ${message.type}`;
    
    // 
    this.listeners.forEach(listener => {
      try {
        listener(message);
      } catch (error) {
        console.error(':', error);
      }
    });
  }

  // 
  addListener(listener) {
    this.listeners.add(listener);
  }

  // 
  removeListener(listener) {
    this.listeners.delete(listener);
  }

  // 
  getStatus() {
    return {
      isConnected: this.isConnected,
      connectionType: this.isConnected ? 'BroadcastChannel' : null,
      debugInfo: this.debugInfo
    };
  }

  // 
  destroy() {
    if (this.channel) {
      this.channel.close();
    }
    
    this.listeners.clear();
    this.isConnected = false;
  }
}

// 
const communicationService = new CommunicationService();

export default communicationService; 